import request from '@/plugin/axios'

export function Page (data) {
  return request({
    url: '/systemInfo/page',
    method: 'post',
    data
  })
}

export function Save(data) {
  return  request({
    url: '/systemInfo/save',
    method: 'post',
    header:{
      "Access-Control-Allow-Origin": "*",
    },
    data
  })
}
